import { Component, HostBinding, OnDestroy, OnInit } from '@angular/core';
import { Loading } from '@sofico-framework/utils';
import { takeUntilDestroy, UntilDestroy } from 'ngx-reactivetoolkit';

@UntilDestroy()
@Component({
  selector: 'sof-centered-spinner',
  template: `
    <div class="overlay"></div>
    <sof-loading></sof-loading>
  `,
  styleUrls: ['./centered-spinner.component.scss']
})
export class CenteredSpinnerComponent implements OnInit, OnDestroy {
  @Loading() loading$;

  private show = false;

  @HostBinding('class.show') get showBinding(): boolean {
    return this.show;
  }

  constructor() {}

  ngOnInit(): void {
    this.loading$
      .pipe(takeUntilDestroy(this))
      .subscribe(loading => (this.show = loading));
  }

  ngOnDestroy(): void {}
}
